import React from "react";
import styled from "styled-components";

const DateStyled = styled.div`
  * {
    background: black;
    color: white;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: monospace;
    font-weight: 10;
  }

  .main {
    width: 100vw;
    position: center;
    top: 50%;
    left: 50%;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    font-family: cursive;
    font-size: 1.5rem;
    align-items: flex-end;
    justify-content: center;
    z-index: 10;
  }
  .DisplayStyled h2 {
    transition: 0.5s;
    cursor: pointer;
    margin: 0 20px;
    padding: 8px;
    height: 50px;
    width: 200px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    grid-column: end;
    background: rgba (255, 255, 255, 0.5);
  }
`;

export default function DateDisplay() {
  const x = new Date();
  const weekDay = [
    "Sunday",
    "Monday",
    "Tuesday",
    "Wednesday",
    "Thursday",
    "Friday",
    "Saturday"
  ];
  const months = [
    "JAN",
    "FEB",
    "MAR",
    "APR",
    "MAY",
    "JUN",
    "JUL",
    "AUG",
    "SEP",
    "OCT",
    "NOV",
    "DEC"
  ];

  const days = weekDay[x.getDay()];
  const month = months[x.getMonth()];
  const date = x.getDate();
  const year = x.getFullYear();
  return (
    <DateStyled>
      <>
        <div className="main">
          <div className="DisplayStyled">
            <h2>{days} </h2>
            <h2>{month}</h2>
            <h2>{date}</h2>
            <h2>{year}</h2>
          </div>
        </div>
      </>
    </DateStyled>
  );
}
